.video {
  background-color: #000000; }
  .video--enabled {
    cursor: pointer; }
    .video--enabled .video__button {
      display: block; }

.video__link {
  display: block;
  width: 100%;
  height: 100%; }
  .video__link::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 61, 133, 0.5);
    transition: background-color 0.3s ease; }

.video__media {
