.video__button:focus .video__button-icon-gradient stop:first-of-type {
  stop-color: #003EB7; }

.video:hover .video__button-icon-gradient stop:not(:first-of-type),
.video__button:focus .video__button-icon-gradient stop:not(:first-of-type) {
  stop-color: #ED0233; }

.video:hover .video__link::after,
.video__button:focus .video__link::after {
